<template>
    <div class="loader">
      <div class="progress"></div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'AppLoader',
  };
  </script>
  
  <style scoped>
  .loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .progress {
    width: 172.8px;
    height: 31.7px;
    border-radius: 28.8px;
    color: rgba(144, 71, 255, 0.67);
    border: 2.9px solid;
    position: relative;
  }
  
  .progress::before {
