      <h1 class="header-title">
        <span class="up">HI!</span>
        <span class="down">I am Priyanshu</span>
      </h1>
      <div class="intro">
      <p class="intro-subtitle">
        <span class="text-slider-items">Full-Stack Developer,Python Developer,Backend Developer</span>
        <strong class="text-slider"></strong>
      </p>
    </div>
    <div class="overlay-itro"></div>
    <router-link to="projects/"><button class="btn btn-outline-light">Visit My Works</button></router-link>
      </div>
