  font-family: 'Baloo Paaji', cursive;
  color: #494e54;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 15%, rgba(255, 255, 255, 1) 100%), url('~@/assets/images/background-image.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.header-title span,
.text-slider {
